import { useRef, useState, useEffect } from "react";
import { useParams } from "react-router";
import config from "../config.json";
import classes from './Admin.module.scss';
import Card from "./Card";

function AdminDoor() {
  const username = useRef();
  const password = useRef();
  const doorNo = useRef();
  const [loggedIn, setLoggedIn] = useState(false);
  const [rerenderHack, setRerenderHack] = useState(false);
  let { doorParam } = useParams();
  const [door, setDoor] = useState(doorParam);
  const [userAnswers, setUserAnswers] = useState([]);
  const [admin, setAdmin] = useState("");
  const [question, setQuestion] = useState("");
  const [pwd, setPwd] = useState("");
  const [localStorageUsername, setLocalStorageUsername] = useState("");

  const [score, setScore] = useState({});
  const [newScore, setNewScore] = useState({});

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (user){
      setPwd(user.pwd);
      setLocalStorageUsername(user.user);
    }
  }, []);

  useEffect(() => {
    fetch(
      `${config.FIREBASE_URL}score.json`
    )
      .then((response) => {
        return response?.json();
      })
      .then((data) => {
        setScore(data);
      });
  }, []);
  
  useEffect(() => {
    fetch(
      `${config.FIREBASE_URL}score.json`
    )
      .then((response) => {
        return response?.json();
      })
      .then((data) => {
        setNewScore(data);
      });
  }, []);


  useEffect(() => {
    fetch(`${config.FIREBASE_URL}openedDoors/${door}.json`)
    .then((response) => {
      return response?.json();
    })
    .then((data) => {
      if (data){
        var keys = Object.keys(data.question)
        setAdmin(data.question[keys].admin);
        setQuestion(data.question[keys].question);
      }
    });
  }, [])

  useEffect(() => {
    if (true) {
      fetch(
        `${config.FIREBASE_URL}answers/${door}.json`
      )
        .then((response) => {
          return response?.json();
        })
        .then((data) => {
          if (data){
            console.log("doorData", data);
            const answerKeys = Object.keys(data.answer);
            const userAnswersTmp = answerKeys.map((x) => {
              return ({user: data.answer[x].username, answer: data.answer[x].answer})
            });
            console.log(userAnswersTmp);
            setUserAnswers(userAnswersTmp);
          }
        });
    }
  }, [loggedIn]);

  function submitHandler(event) {
    event.preventDefault();
    const user = username.current.value;
    const pwd = password.current.value;
    
    fetch(
      `${config.FIREBASE_URL}users/${pwd}${user}.json`
    )
      .then((response) => {
        return response?.json();
      })
      .then((data) => {
        if (data) {
          console.log("user?", user, admin);
          if (user === admin){
            setLoggedIn(true);
          }
          else{
            throw new Error("nej");
          }
        } else {
          throw new Error("nej");
        }
      })
      .catch((err) => {
        alert(err.message);
      });
  }

  function givePoints(username, maybeGive) {
    var tmpScore = newScore[username];
    if (maybeGive === "+") {
      tmpScore++;
    } else {
      tmpScore--;
    }
    console.log(tmpScore);
    console.log(score[username]);
    newScore[username] = tmpScore;
    setNewScore(newScore);
    setRerenderHack(!rerenderHack);
  } 

  function updateUsers(event) {
    event.preventDefault();
    fetch(
      `${config.FIREBASE_URL}score.json`,
      {
        method: "PATCH",
        body: JSON.stringify(newScore),
        headers: {
          "Content-Type": "application/json",
        },
      }
    );
    
    const adminBody = {admin: admin, door: door, oldScore: score, newScore: newScore, dateTime: new Date()}
    fetch(
      `${config.FIREBASE_URL}admin.json`,
      {
        method: "POST",
        body: JSON.stringify(adminBody),
        headers: {
          "Content-Type": "application/json",
        },
      }
    ).then(setScore(newScore))
    
  }

  function NotLoggedIn() {
    return (
      <form onSubmit={submitHandler}>
        <input type="text" value={localStorageUsername} onChange={(val) => setLocalStorageUsername(val.value)} ref={username} />
        <input type="password" value={pwd}onChange={(val) => setPwd(val.value)}  ref={password} />
        <button>Logg inn</button>
      </form>
    );
  }

  function UserAnswer({user, answer}){
    return (
      <>
      <div className={classes.user}>{user}</div>
      <div>{answer}</div>
      </>
    )
  }

  function ScoreBoardEntry({username}){
    return (
      <>
      <tr key={username}>
        <td>
          {username}({score[username]}) {newScore[username]}{" "}
        </td>
        <td>
          <button id={username} onClick={() => {
            givePoints(username, "+")
          }}>
            +1
          </button>
          <button id={username} onClick={() => {
            givePoints(username, "-")
          }}>
            -1
          </button>
        </td>
      </tr>
      </>
    )
  }

  function LoggedIn() {
    setDoor(doorParam);
    const usernames = Object.keys(score);
    return (
      <>
      <Card>
        <h2>Admin - luke {doorParam}</h2>
        <div>{question}</div>
        <div>
          <ul>
          {userAnswers.map((userAnswer) => {
            return <li><UserAnswer user={userAnswer.user} answer={userAnswer.answer}/></li>
          })}
          </ul>
        </div>
      </Card>
      <Card>
        <h2>Poeng</h2>
        <form onSubmit={updateUsers}>
          <table>
            <tbody>
              {usernames.map((username) => (
                <ScoreBoardEntry username={username} />
              ))}
            </tbody>
          </table>
          <button>Oppdater</button>
        </form>
      </Card>
      </>)
  }

  return (
    <div>
      {!loggedIn && <NotLoggedIn />}
      {loggedIn && <LoggedIn />}
    </div>
  );
}

export default AdminDoor;
