import { useState, useEffect } from "react";
import classes from "./CountDown.module.scss"

function twoDigitTime(timePart) {
  return (timePart < 10 ? "0" : "") + timePart;
}

function countTime() {
  const eta = new Date(2023, 11, 1, 0, 0, 0).getTime();
  const now = new Date().getTime();
  const diff = eta - now;
  const hours = Math.floor((diff / (1000 * 60 * 60)) % 24);
  const minutes = Math.floor((diff / (1000 * 60)) % 60);
  const seconds = Math.floor((diff / 1000) % 60);
  if (diff > 0)
    return {
      days: Math.floor(diff / (1000 * 60 * 60 * 24)),
      hours: twoDigitTime(hours),
      minutes: twoDigitTime(minutes),
      seconds: twoDigitTime(seconds),
    };
  return {
    days: 0,
    hours: 0,
    minutes: 0,
    seconds: 0,
  };
}

export function CountDown() {
  const [timeLeft, setTimeLeft] = useState(countTime());
  useEffect(() => {
    const timer = setTimeout(() => {
      setTimeLeft(countTime());
    }, 1000);
    return () => clearTimeout(timer);
  });

  return (
    <div className={classes.countDown}>
      {timeLeft.days} dager {timeLeft.hours} timer {timeLeft.minutes} minutter
      og {timeLeft.seconds} sekunder
    </div>
  );
}
