import AddDoorForm from "./AddDoorForm";
import { useNavigate } from "react-router-dom";
import config from "../config.json"

function AddDoor() {
  const navigate = useNavigate();

  function checkPasswordAndSubmit(answer) {
    const dbName = `${answer.password}${answer.username}`;
    fetch(
      `${config.FIREBASE_URL}users/${dbName}.json`
    )
      .then((response) => {
        console.log(response);
        return response?.json();
      })
      .then((data) => {
        if (data) {
          addDoor(answer);
        } else {
          throw new Error("Brukernavn eller passord er feil");
        }
      })
      .catch((err) => {
        alert(err.message);
      });
  }

  function addDoor(door) {
    fetch(
      `${config.FIREBASE_URL}doorsQnA/${door.id}/qna.json`,
      {
        method: "POST",
        body: JSON.stringify(door),
        headers: {
          "Content-Type": "application/json",
        },
      }
    )
      .then(
        fetch(
          `${config.FIREBASE_URL}doorsQ/${door.id}.json`,
          {
            method: "POST",
            body: JSON.stringify({ question: door.question, admin: door.username }),
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
      )
      .then(
        fetch(
          `${config.FIREBASE_URL}doorIds/${door.id}.json`,
          {
            method: "POST",
            body: JSON.stringify(door.id),
            headers: {
              "Content-Type": "application/json",
            },
          }
        )
      )
      .then(navigate("/luke-opprettet"));
  }

  return (
    <div>
      <section>
        <AddDoorForm onAddedQnA={checkPasswordAndSubmit} />
      </section>
    </div>
  );
}

export default AddDoor;
