import classes from "./ResponseForm.module.css";
import { useRef, useState, useEffect } from "react";

import Card from "./Card";

function AddDoorForm(props) {
  const qRef = useRef();
  const aRef = useRef();
  const userRef = useRef();
  const pwdRef = useRef();
  const [pwd, setPwd] = useState("");
  const [username, setUsername] = useState("");

  useEffect(() => {
    const user = JSON.parse(localStorage.getItem("user"));
    if (user){
      setPwd(user.pwd);
      setUsername(user.user);
    }
  }, []);

  function submitHandler(event) {
    event.preventDefault();

    const submittedQnA = {
      id: Math.floor(Math.random() * 10000),
      question: qRef.current.value,
      answer: aRef.current.value,
      submittedAt: new Date(),
      username: userRef.current.value,
      password: pwdRef.current.value,
    };
    try {
      props.onAddedQnA(submittedQnA);
    } catch (err) {
      console.log(err);
    }
  }

  return (
    <Card>
      <form className={classes.form} onSubmit={submitHandler}>
        <h3>Legg inn en ny luke</h3>
        <div className={classes.control}>
          <textarea
            type="text"
            id="question"
            required
            rows="5"
            ref={qRef}
            placeholder="Spørsmål"
          />
        </div>
        <div className={classes.control}>
          <textarea
            type="text"
            id="answer"
            rows="5"
            required
            ref={aRef}
            placeholder="Svar"
          />
        </div>
        <div className={classes.control}>
        <input type="text" ref={userRef} value={username} onChange={(val) => setUsername(val.value)} placeholder="Brukernavn"/>
        <input type="password" ref={pwdRef} value={pwd} onChange={(val) => setPwd(val.value)} placeholder="Passord"/>
        </div>
        <div id="error"> </div>
        <div className={classes.actions}>
          <button>Send inn</button>
        </div>
      </form>
    </Card>
  );
}

export default AddDoorForm;
